import React, { useState, useEffect } from 'react';
import { Form, Input, Button, InputNumber, Radio, Divider, Descriptions, Select, Badge, Table, Row, Col, AutoComplete } from 'antd';
import useGetPharmacyMedicines from '../../../state/pharmacy/hooks/useGetAllPharmacyMedicines';
import useTestSearch from '../../../state/addMedicine/hooks/useSearchTest';
import { getItemUnitsList } from '../../../state/pharmacy/queries';
import { convertUnit } from '../../../utils/unitConverter';
const { Option } = Select;

const AddItem = (props) => {
    const [form] = Form.useForm();
    const medicineMap = new Map();
    const testMap = new Map();
    let options = [
        // { value: 'light', label: 'Light' },
        // { value: 'bamboo', label: 'Bamboo' },
    ];
    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    };

    const [selected, setSelected] = useState("medicine");
    const [selectedValue, setSelectedValue] = useState("");

    const [medicines, isLoading, setMedicineSearch] = useGetPharmacyMedicines();
    const [tests, isLoading1, setTestSearch] = useTestSearch();
    const [itemUnits, setItemUnits] = useState([]);
    const [amountLabel, setAmountLabel] = useState("Amount");

    // const [options, isLoading, setMedicineSearch] = useGetPharmacyMedicines();
    useEffect(() => {
        setMedicineSearch();
        setFormdefaultValue();
        intialiseItemUnits().then(data=> clearForm);
    }, []);
    function intialiseItemUnits() {
        return new Promise((resolve, reject)=> {
            getItemUnitsList().then(data => {
                if (Array.isArray(data)) {
                    setItemUnits(data);
                    form.setFieldsValue({
                        user: {
                            unit: data[0].unitName
                        }
                    });
                    resolve(data);
                }
            });
        });

    }

    function setFormdefaultValue() {
        form.setFieldsValue({
            user: {
                quantity: 1,
                amount: 0,
            }
        });
    }
    if (selected == "test") {
        options = [...tests.map(test => {
            testMap.set(test.testName, test);
            return { value: test.testName, label: test.testName };
        })];
    } else if (selected == "medicine") {
        if (medicines.length > 0) {

            medicines.forEach(medicine => {
                medicineMap.set(medicine.medicineName, medicine);
                options.push({ value: medicine.medicineName, label: medicine.medicineName });
            });
        }
    } else if (selected == "consultation") {
        if (medicines.length > 0) {
            medicines.forEach(medicine => {
                options.push({ value: medicine.medicineName, label: medicine.medicineName });
            });
        }
    }

    function onFinish(value) {
        console.log("arakadra", value.user.name);
        const obj = {
            itemId: null,
            name: value.user.name,
            quantity: value.user.quantity,
            unit: value.user.unit,
            itemType: selected,
            amount: value.user.amount,
            barcodeNum: '',
            actualUnit: value.user.unit
        }
        if (selected == "medicine") {
            const medicinedetail = medicineMap.get(value.user.name);
            const selectedUnit = value.user.unit;

            // if (medicinedetail.unit != selectedUnit) {
            //     let convertedQuantity = convertUnit(selectedUnit, medicinedetail.unit);
            //     console.log("converted quantity", convertedQuantity);
            //     if (convertedQuantity != -100) {
            //         obj["amount"] = Number.parseFloat(convertedQuantity) *  medicinedetail.salePrice;
            //     }
            // }
            // obj["amount"] = medicinedetail.salePrice;
            obj["itemId"] = medicinedetail.medicineId;
            obj["actualUnit"] = medicinedetail.unit;
            obj.barcodeNum = medicinedetail.barcodeNum;

        } else if (selected == "test") {
            const testdetail = testMap.get(value.user.name);
            obj["amount"] = testdetail.price;
            obj["itemId"] = null;
        }
        // console.log(obj);
        props.onItemAdded(obj);
    }
    function onItemTypeSelect(value) {
        setSelected(value);
        setAmountLabel("Amount");
        clearForm();
        if (value == "test") {
            setTestSearch();
        }
    }
    function onSelect(data) {
        if (selected == "medicine") {
            const medicinedetail = medicineMap.get(data);
            if (medicinedetail) {
                form.setFieldsValue({
                    user: {
                        amount: medicinedetail.salePrice,
                        unit: medicinedetail.unit
                    }
                });
                setAmountLabel("Amount per " + medicinedetail.unit);
            }

        } else if (selected == "test") {
            const testdetail = testMap.get(data);
            if (testdetail) {
                form.setFieldsValue({
                    user: {
                        amount: testdetail.salePrice,
                    }
                });
            }
        }
    }

    function clearForm() {
        form.setFieldsValue({
            user: {
                name : '',
                quantity : 0,
                unit : itemUnits[0].unitName,
                amount : 0,
            }
        });
    }
    return (
        <Form form={form} {...layout} name="nest-messages" onFinish={onFinish} >
            <Form.Item name={['user', 'name']} label="Item name">
                <AutoComplete
                    dropdownClassName="certain-category-search-dropdown"
                    autoFocus={true}
                    allowClear={true}
                    dropdownMatchSelectWidth={200}
                    style={{ width: '100%' }}
                    options={options}
                    onChange={onSelect}
                    filterOption={(inputValue, option) => option.value.toUpperCase().indexOf(inputValue.toUpperCase()) !== -1}
                >
                    <Input.Search size="default" placeholder="Item name" />
                </AutoComplete>
            </Form.Item>
            <Form.Item name={['user', 'quantity']} label="Quantity" rules={[{ type: 'number' }]}>
                <InputNumber />
            </Form.Item>
            <Form.Item name={['user', 'unit']} label="Unit" >
                <Select placeholder="Unit">
                    {itemUnits.map(item => (
                        <Option key={item.unitName}>{item.unitName}</Option>
                    ))}
                </Select>
            </Form.Item>
            <Form.Item name={['user', 'amount']} label={amountLabel} rules={[{ type: 'number' }]}>
                <InputNumber disabled={(selected == "medicine")} />
            </Form.Item>
            <Form.Item name={['user', 'itemType']} label="Item type" >
                <Select onSelect={onItemTypeSelect} defaultValue={selected} placeholder="Item type">
                    <Option value="medicine">Inventory</Option>
                    <Option value="consulation">Others</Option>
                </Select>
            </Form.Item>
            <Form.Item>
                <Button type="primary" htmlType="submit">
                    Add Item
                </Button>
            </Form.Item>
        </Form>);
};
export default AddItem;